/** InfoBubble */
.H_ib {
    position: absolute;
    left: .91em;
    background: #fff;
    color: #fff;
    font-size: 2em;
    line-height: 1em;
    fill: #fff;
    left: -100%;
  }
  .H_ib_body {
    background: #fff;
    position: absolute;
    bottom: .5em;
    padding: 0 1.2em 0 0;
    border-radius: .2em;
    margin-right: -1em;
    right: 0;
  }
  svg.H_icon{
      fill: #000;
  }
  .content-info-bubble{
      color: #000;
  }
  .H_btn{
      background: #fff;
  }
  
